import { useState, useEffect } from "react";
import { Button, Paper, TextField, Typography } from "@material-ui/core";
import { SnackbarProvider, useSnackbar } from "notistack";
import styled from "styled-components";
import axios from "axios";
import images from "../../assets/11.jpg";
import cardImg from "../../assets/10.jpg";
import "./styled.css";

function SignIn() {
  const { enqueueSnackbar } = useSnackbar();
  const [data, setData] = useState({
    furstName: "",
    password: "",
  });

  const handleSnacbar = (variant) => () => {
    enqueueSnackbar("Пароль введен неверно !", { variant });
  };
  const handleSnacbarLogin = (variant) => () => {
    enqueueSnackbar("Это имя недоступно !", { variant });
  };
  const FetchProduct = async (e) => {
    e.preventDefault();

    const instance = axios.create({
      baseURL: "http://89.223.71.112:9898/",
    });
    await instance
      .post(
        "signInAdmin",
        { login: data.furstName, password: data.password },
        {
          headers: {
            "Content-Type": "application/json",
          },
        }
      )
      .then((res) => {
        localStorage.setItem("token", JSON.stringify(res.data.data.token));
        localStorage.setItem("tovar", JSON.stringify(res.data.data));
        window.location.href = "/";
        console.log(res)
      })
      .catch((err) => {
        if (err.response.data.code === 50027) {
          handleSnacbar("error")();
        } else if (err.response.data.code === 50026) {
          handleSnacbarLogin("error")();
        }
        console.log(err.response);
      });
  };

  return (
    <Wrapper className="Wrapper  animate__animated animate__fadeIn">
      <PaperStyled className="animate__animated animate__bounceInRight">
        <RowStyled>
          <img src={cardImg} />
        </RowStyled>
        <RowLeftStyled>
          <form onSubmit={FetchProduct}>
            <Typography
              style={{
                marginTop: "40px",
                marginBottom: "30px",
                textShadow: "0px 0px 2px blue",
                textAlign: "center",
              }}
              variant="h4"
            >
              Sign In
            </Typography>
            <TextField
              style={{ marginBottom: "20px" }}
              fullWidth
              id="outlined-basic"
              type="text"
              label="Login"
              variant="outlined"
              onChange={(e) => setData({ ...data, furstName: e.target.value })}
            />
            <TextField
              style={{ marginBottom: "20px" }}
              fullWidth
              id="outlined-basic"
              type="password"
              label="password"
              variant="outlined"
              onChange={(e) => setData({ ...data, password: e.target.value })}
            />
            <StyledButton variant="outlined" type="submit">
              {" "}
              login{" "}
            </StyledButton>
          </form>
        </RowLeftStyled>
      </PaperStyled>
    </Wrapper>
  );
}

export default SignIn;

const Wrapper = styled.div`
  max-width: 100%;
  height: 100vh;
  background-color: #f5f7fa;
  display: flex;
  justify-content: center;
  align-items: center;
`;
const PaperStyled = styled(Paper)`
  width: 800px;
  height: 450px;
  display: flex;
  overflow: hidden;
`;
const RowStyled = styled.div`
  width: 800px;
  height: 100%;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transform: skew(-10deg, 0deg  );
    margin-left: -40px;
  }
`;
const RowLeftStyled = styled.div`
  padding: 30px;
`;

export const StyledButton = styled(Button)`
  && {
    margin-top: 20px;
    border: 2px solid #3545a3;
    display: block;
    width: 100%;
    padding: 8px 25px;
    border-radius: 10px;
    font-family: "Inter", sans-serif;
    font-family: "Open Sans", sans-serif;
    font-family: "Padauk", sans-serif;
    font-weight: 800;
    &:hover {
      color: white;
      background: #2a3a96;
    }
  }
`;
